@use "global" as *;

.header {
  width: 100%;
  height: 80px;
  padding: 0 4rem;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);

  @include mq("md") {
    height: 60px;
    padding: 0 2rem;
  }
}

.header_inner {
  width: 192rem;
  max-width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;

  @include mq("md") {
    height: 60px;
  }
}

.header_nav {
  display: flex;
  align-items: center;
  padding-top: 0.5rem;

  @include mq("lg") {
    display: none;
  }
}

.header_nav_list {
  display: flex;
  height: 5.4rem;
  margin-right: 6.4rem;
}

.header_nav_link {
  font-size: 1.1rem;
  line-height: 1.8rem;
  font-weight: bold;
  position: relative;

  &.is_active {
    color: $main_color;
  }
  &.is_active::after {
    width: 2rem;
  }

  &:not(:first-child) {
    margin-left: 2.4rem;
  }

  a {
    display: block;
    padding-top: 3rem;
    padding-bottom: 0.6rem;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      width: 2.4rem;
      height: 2.4rem;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  &::after {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    content: "";
    display: block;
    width: 0;
    height: 2px;
    background-color: $main_color;
    transition: all 0.3s ease;
  }

  &:hover {
    color: $main_color;
  }
  &:hover::after {
    width: 2rem;
  }
}

.header_nav_link_01 a::before {
  background: url(/images/common/icon-01_black.svg) no-repeat center center / cover;
}
.header_nav_link_01.is_active a::before {
  background: url(/images/common/icon-01.svg) no-repeat center center / cover;
}
.header_nav_link_01 a:hover::before {
  background: url(/images/common/icon-01.svg) no-repeat center center / cover;
}
.header_nav_link_02 a::before {
  background: url(/images/common/icon-02.svg) no-repeat center center / cover;
}
.header_nav_link_02.is_active a::before {
  background: url(/images/common/icon-02blue.svg) no-repeat center center / cover;
}
.header_nav_link_03 a::before {
  background: url(/images/common/icon-03.svg) no-repeat center center / cover;
}
.header_nav_link_03.is_active a::before {
  background: url(/images/common/icon-03blue.svg) no-repeat center center / cover;
}
.header_nav_link_04 a::before {
  background: url(/images/common/icon-04.svg) no-repeat center center / cover;
}
.header_nav_link_04.is_active a::before {
  background: url(/images/common/icon-04blue.svg) no-repeat center center /
    cover;
}
.header_nav_link_05 a::before {
  background: url(/images/common/icon-05.svg) no-repeat center center / cover;
}
.header_nav_link_05.is_active a::before {
  background: url(/images/common/icon-05blue.svg) no-repeat center center / cover;
}
.header_nav_link_06 a::before {
  background: url(/images/common/icon-06.svg) no-repeat center center / cover;
}
.header_nav_link_06.is_active a::before {
  background: url(/images/common/icon-06blue.svg) no-repeat center center / cover;
}

.header_nav_link_02 a:hover::before {
  background: url(/images/common/icon-02blue.svg) no-repeat center center /
    cover;
}
.header_nav_link_03 a:hover::before {
  background: url(/images/common/icon-03blue.svg) no-repeat center center /
    cover;
}
.header_nav_link_04 a:hover::before {
  background: url(/images/common/icon-04blue.svg) no-repeat center center /
    cover;
}
.header_nav_link_05 a:hover::before {
  background: url(/images/common/icon-05blue.svg) no-repeat center center /
    cover;
}
.header_nav_link_06 a:hover::before {
  background: url(/images/common/icon-06blue.svg) no-repeat center center /
    cover;
}

.header_logo {
  width: 270px;
  margin-right: auto;
}

.header_nav_address {
  font-size: 1.2rem;
  font-weight: 500;
}

.header_nav_tel {
  font-size: 3.2rem;
  font-weight: bold;
  letter-spacing: -0.64px;
  color: $main_color;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 2.8rem;
    height: 2.8rem;
    background: url(/images/common/icon_tel.svg) no-repeat center center / cover;
    top: 50%;
    left: -3rem;
    transform: translateY(-50%);
  }
}

.drawer_button {
  width: 32px;
  height: 32px;
  position: relative;
  z-index: 100;
  cursor: pointer;
  display: none;

  @include mq("lg") {
    display: block;
  }

  span {
    display: inline-block;
    width: 27px;
    height: 2.25px;
    background-color: $main_color;
    border-radius: 8px;
    position: absolute;
    left: 2px;
    transition: all 0.3s ease;
  }
  span:nth-of-type(1) {
    top: 7px;
  }
  span:nth-of-type(2) {
    top: 15px;
  }
  span:nth-of-type(3) {
    top: 23px;
  }
}

.drawer_button.is_active span {
  background-color: #fff;
}

.drawer_button.is_active span:nth-of-type(1) {
  transform: translateY(8px) rotate(-45deg);
}

.drawer_button.is_active span:nth-of-type(2) {
  opacity: 0;
}

.drawer_button.is_active span:nth-of-type(3) {
  transform: translateY(-8px) rotate(45deg);
}
