@use "global" as *;

.main_visual {
  max-width: 100%;
  margin-top: 4rem;

  @include mq("md") {
    margin-top: 2rem;
  }
}

.main_visual_container {
  padding: 0 6.2rem;
  margin: auto;
  position: relative;
  max-width: 192rem;

  @include mq("lg") {
    padding: 0 4rem;
  }
  @include mq("sm") {
    padding: 0 2rem;
  }
}

.slick-list {
  @include mq("md") {
    max-height: 600px;
  }
}

.slider {
  margin: 0 auto;
  position: relative;
  max-width: 192rem;

  @include mq("md") {
    width: 100%;
  }

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.1);
    top: 0;
    left: 0;
    right: 0;
    border-radius: 25px;

    @include mq("sm") {
      border-radius: 0;
    }
  }
}

.slide-dots {
  text-align: center;
  height: 0;
  li {
    margin: 0;
    width: 1.8rem;
    height: 1.8rem;
    position: relative;
    top: -5.3rem;
    left: -2.8rem;
    button {
      text-indent: -9999px;
      &:before {
        content: "●";
        color: #c2c2c2;
        font-size: 0.8rem;
        text-indent: -0.7rem;
        position: absolute;
        top: 0;
        left: 1.1rem;
      }
    }
    &.slick-active {
      button:before {
        content: "●";
        color: $main_color;
      }
    }
    @include mq("sm") {
      top: -5.7rem;
      left: -1.9rem;
      height: 1.4rem;
    }
  }
}

.slide-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 6.8rem;
  height: 6.8rem;
  z-index: 1;
  cursor: pointer;

  @include mq("md") {
    width: 4.5rem;
    height: 4.5rem;
  }
}

.prev-arrow {
  left: -3rem;

  @include mq("md") {
    left: -2.3rem;
  }
}

.next-arrow {
  right: -3.4rem;

  @include mq("md") {
    right: -2.3rem;
  }
}

.reserve_button {
  width: 9.2rem;
  height: 12rem;
  background-color: $main_color;
  border-radius: 20px 0 0 20px;
  padding: 2rem 1.7rem 1.7rem 1.7rem;
  display: block;
  position: fixed;
  z-index: 10;
  top: 18rem;
  right: 0;
  transition: background-color 0.3s ease;

  &:hover {
    background-color: $sub_color-01;
  }

  @include mq("lg") {
    display: none;
  }

  img {
    width: 4.8rem;
    margin: 0 auto;
  }
  span {
    font-size: 1.4rem;
    font-weight: bold;
    color: #fff;
    margin-top: 1.2rem;
    display: block;
  }
}

.main_visual_top_text {
  width: 41.5rem;
  height: 12rem;
  position: absolute;
  bottom: 5.2rem;
  left: 12.2rem;

  @include mq("sm") {
    width: vw(375, 265);
    left: 11%;
    bottom: 0;
    top: 74%;
    height: 74px;
  }
}

.slick-dotted.slick-slider {
  margin-bottom: 0px;
}
