@use "global" as *;

.single_blog_title {
  font-size: 2.8rem;
  letter-spacing: 2.24px;
  font-weight: bold;

  @include mq('sm') {
    font-size: 2rem;
    letter-spacing: 1.6px;
  }
}

.lower_blog_container.single_blog_container {
  align-items: stretch;

  @include mq('md') {
    padding: 0 2rem;
  }
}

.single_blog_meta {
  display: flex;
  align-items: center;
  margin-top: 1.4rem;

  @include mq('sm') {
    margin-top: 2rem;
  }
}

.single_blog_time {
  font-size: 1.6rem;
  font-weight: 500;
  color: $main_color;
  margin-right: 3.2rem;
  padding-left: 2rem;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 1.4rem;
    height: 1.4rem;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    background: url(/images/blog/icon-time.svg) no-repeat center center / cover;
  }
}

.blog_category.lower_blog_category.single_blog_category {
  margin: 0;
}

.single_blog_content {
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 1.71428;
  margin-top: 5.4rem;
  width: 98.5%;

  a {
    color: $main_color;
    text-decoration: underline;
  }
}

.single_blog_content_2 {
  margin-top: 6.4rem;
  font-size: 2.4rem;
  font-weight: bold;
  letter-spacing: 1.92px;
  color: $main_color;
  padding-left: 2.8rem;
  position: relative;

  @include mq('sm') {
    font-size: 1.8rem;
    letter-spacing: 1.44px;
    padding-left: 2.3rem;
  }

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 0.8rem;
    height: 5.2rem;
    transform: translateY(-50%);
    background-color: $main_color;

    @include mq('sm') {
      height: 4rem;
    }
  }
}

.single_blog_img {
  margin-top: 2rem;

  @include mq('sm') {
    margin-top: 2.4rem;
  }
}

.single_blog_content_3 {
  margin-top: 4rem;
  font-size: 2rem;
  font-weight: bold;
  letter-spacing: 1.6px;
  color: $main_color;
  padding-bottom: 0.6rem;
  border-bottom: 1px solid $main_color;
  width: 98.5%;

  @include mq('sm') {
    font-size: 1.6rem;
    letter-spacing: 1.28px;
    width: 100%;
  }
}

.single_blog_content_3_text {
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 1.71428;
  margin-top: 1.5rem;
  width: 98.5%;
}

.single_blog_content_4 {
  font-size: 1.6rem;
  font-weight: bold;
  letter-spacing: 1.28px;
  color: $main_color;
  margin-top: 5.7rem;

  @include mq('sm') {
    font-size: 1.4rem;
    letter-spacing: 1.12px;
    
  }
}

.single_blog_content_4_text {
  margin-top: 1.4rem;
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 1.71428;
  width: 98.5%;
}

.single_blog_content_4_list {
  margin-top: 1.2rem;
  padding-left: 2.8rem;

  li {
    font-size: 1.6rem;
    font-weight: 500;
    margin-bottom: 1rem;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      width: 0.5rem;
      height: 0.5rem;
      top: 50%;
      left: -2%;
      transform: translateY(-50%);
      background-color: #393939;
      border-radius: 50%;
    }
  }
}

.blog_contents.single_blog_contents {
    margin-right: 2%;

    @include mq('sm') {
      width: 100%;
    }
}

.blog_category.lower_blog_category.single_blog_category {
  padding: 8px 12px;
  line-height: 1;
  height: auto;
}
