@use "global" as *;

.reserve_button_sp {
  position: fixed;
  z-index: -10;
  bottom: 0;
  left: 0;
  right: 0;
  height: 60px;
  padding-left: 16px;
  padding-bottom: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  background-color: rgba(255, 255, 255, 0.85);
  opacity: 0;
  transition: opacity 0.3s ease;

  @include mq("lg") {
    z-index: 20;
    opacity: 1;
  }
}

.reserve_button_tel {
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 0px;
  color: $main_color;
  position: relative;
  padding-left: 16px;
  margin-right: 12px;

  &::before {
    content: "";
    position: absolute;
    width: 18px;
    height: 18px;
    background: url(/images/common/icon_tel.svg) no-repeat center center / cover;
    top: 50%;
    left: -4px;
    transform: translateY(-50%);
  }
  &::after {
    content: "(年中無休 AM9:00〜PM22:00)";
    position: absolute;
    font-size: 10px;
    color: $text_color;
    top: 25px;
    left: 8px;
  }
}

.reserve_button_web {
  width: 157px;
  height: 47px;
  background-color: $main_color;
  border-radius: 8px;
  font-size: 16px;
  line-height: 1;
  padding: 10px 0 0 70px;
  display: block;
  color: #fff;
  font-weight: bold;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    background: url(/images/common/icon-pc.svg) no-repeat center center / cover;
    width: 43px;
    height: 25px;
    top: 50%;
    transform: translateY(-50%);
    left: 16px;
  }

  span {
    display: block;
    font-size: 12px;
    line-height: 1.25;
    padding-left: 9px;
  }
}
