@use "global" as *;

.medical {
  margin-top: 14rem;

  @include mq("sm") {
    margin-top: 10rem;
  }
}

.medical_inner {
  background-color: $sub_color-02;
  padding-bottom: 8.5rem;
  position: relative;

  @include mq("sm") {
    padding-top: 3rem;
    padding-bottom: 9.5rem;
  }

  &::before {
    content: "";
    position: absolute;
    width: 5.7rem;
    height: 5.7rem;
    background: url(/images/common/deco-kirakira2-w.svg) no-repeat center center /
      cover;
    bottom: 0rem;
    left: 4rem;

    @include mq("sm") {
      width: 2.5rem;
      height: 2.5rem;
      left: 1rem;
      bottom: 1rem;
    }
  }
  &::after {
    content: "";
    position: absolute;
    width: 12.5rem;
    height: 13.7rem;
    background: url(/images/common/deco-kirakira-w.svg) no-repeat center center /
      cover;
    bottom: -2rem;
    right: 2rem;

    @include mq("sm") {
      width: 5.3rem;
      height: 5.8rem;
      right: 1rem;
      bottom: 0;
    }
  }
}

.medical_contents {
  display: flex;
  justify-content: center;
  margin-top: 5.5rem;

  @include mq("md") {
    display: block;
  }
  @include mq("sm") {
    margin-top: 4.5rem;
  }
}

.medical_wrap {
  width: 46rem;
  height: 28.8rem;
  overflow: hidden;
  border-radius: 20px;

  @include mq("md") {
    width: 80%;
    margin: 0 auto;
  }
  @include mq("sm") {
    width: 89%;
    height: 20.9rem;
  }

  &:not(:last-child) {
    margin-right: 8rem;

    @include mq("md") {
      margin: 0 auto 2rem;
    }
  }
}

.medical_line {
  width: 44rem;
  height: 26.8rem;
  border-radius: 20px;
  transition: all 0.2s ease;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;

  @include mq("md") {
    width: 97%;
  }
  @include mq("sm") {
    width: 93%;
    height: 90%;
  }
}

.medical_box {
  display: block;
  width: 100%;
  height: 100%;
  position: relative;
  transition: all 0.6s ease;

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba($color: #000000, $alpha: 0.2);
    display: block;
    top: 0;
    transition: all 0.6s ease;
  }

  &:hover::after {
    background-color: rgba($color: #000000, $alpha: 0.3);
  }

  &:hover {
    .medical_line {
      border: 2px solid #fff;
    }
  }
}

.medical_img_01 {
  &:hover::before {
    transform: scale(1.1, 1.1);
  }

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(/images/common/medical_1.png);
    background-repeat: no-repeat;
    background-size: cover;
    display: block;
    top: 0;
    transition: all 0.6s ease;
  }
}

.medical_img_02 {
  &:hover::before {
    transform: scale(1.1, 1.1);
  }

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(/images/common/medical_2.png);
    background-repeat: no-repeat;
    background-size: cover;
    display: block;
    top: 0;
    transition: all 0.6s ease;
  }
}

.medical_center {
  width: 25rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  @include mq("sm") {
    width: 18.5rem;
  }
}

.medical_center_01 {
  @include mq("sm") {
    padding-top: 1rem;
  }
}

.medical_center_02 {
  padding-top: 2.8rem;
  @include mq("sm") {
    padding-top: 2.8rem;
  }
}

.medical_title {
  font-size: 2.8rem;
  letter-spacing: 2.24px;
  color: #fff;
  font-weight: bold;
  text-align: center;
  position: relative;

  @include mq("sm") {
    font-size: 2rem;
    letter-spacing: 1.6px;
  }

  &::after {
    content: "";
    position: absolute;
    background-color: #fff;
    border-radius: 30px;
    width: 4rem;
    height: 0.2rem;
    top: 6rem;
    left: 50%;
    transform: translateX(-50%);

    @include mq("sm") {
      width: 3rem;
      top: 4.8rem;
    }
  }
}

.medical_text {
  margin-top: 4rem;
  font-size: 1.6rem;
  letter-spacing: 1.28px;
  font-weight: bold;
  color: #fff;
  text-align: center;

  @include mq("sm") {
    font-size: 1.2rem;
    letter-spacing: 0.96px;
  }
}

.medical_message {
  margin-top: 6.1rem;
  width: 100rem;
  max-width: 100%;
  background-color: #fff;
  margin: 5.5rem auto 0;
  padding: 4rem;
  font-size: 1.4rem;
  line-height: 2;

  @include mq("md") {
    width: 80%;
  }
  @include mq("sm") {
    width: 89%;
    margin-top: 2rem;
    padding: 2rem 1.8rem;
    line-height: 1.714;
  }

  span {
    color: $accent_color-01;
  }
}

/*------------------------
下層ページ
-------------------------*/

.medical.lower_medical {
  margin-top: 8rem;

  @include mq('sm') {
    margin-top: 6.2rem;
  }
}

.lower_medical_inner {
  width: 99.8rem;
  margin: auto;

  @include mq('sm') {
    width: 100%;
    padding: 0 2rem;
  }
}

.lower_medical_container {
  margin-bottom: 16rem;

  @include mq('sm') {
    margin-bottom: 10rem;
  }
}

.lower_medical_box {
  &:nth-of-type(1) {
    margin-bottom: 7.8rem;

    @include mq('sm') {
      margin-bottom: 8.3rem;
    }
  }
}

.medical_course {
  font-size: 2.8em;
  font-weight: bold;
  letter-spacing: 2.24px;
  position: relative;
  margin-bottom: 3rem;

  @include mq('sm') {
    font-size: 2rem;
  }
  
  &::after {
    content: "";
    position: absolute;
    padding: 0.8rem 1.2rem;
    top: 50%;
    transform: translateY(-50%);
    font-size: 1.4rem;
    letter-spacing: 1.14px;
    line-height: 1.5;
    color: #fff;
    border-radius: 24px;
    margin-left: 2.1rem;
  }
  &.medical_course_general::after {
    content: "保険対象";
    background-color: $main_color;
  }
  &.medical_course_special::after {
    content: "実費";
    background-color: $accent_color-01;
  }
}

.medical_tag_wrap {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;

  @include mq('sm') {
    display: block;
  }
}

.medical_tag {
  display: inline-block;
  padding: 1.6rem 2.95rem;
  border-radius: 12px;
  border: 2px solid $main_color;
  background-color: #fff;
  font-size: 2.4rem;
  font-weight: bold;
  color: $main_color;
  letter-spacing: 1.92px;
  text-align: center;

  @include mq('sm') {
    display: block;
    margin-bottom: 1.6rem;
    font-size: 2rem;
    padding: 1.35rem 2.95rem;
  }
}

.general {
  margin-bottom: 16rem;

  @include mq('sm') {
    margin-bottom: 10.5rem;
  }

  &.special {
    margin-bottom: 15rem;

    @include mq('sm') {
      margin-bottom: 10.2rem;
    }
  }
}

.general_inner {
  background-color: #dff1fd;
  padding-bottom: 8.1rem;
  position: relative;

  @include mq('sm') {
    padding: 0 2rem 9.3rem 2rem;
  }

  &::before {
    content: "";
    position: absolute;
    width: 5.7rem;
    height: 5.7rem;
    background: url(/images/common/deco-kirakira2-w.svg) no-repeat center center /
      cover;
    bottom: 0rem;
    left: 4rem;

    @include mq("sm") {
      width: 2.5rem;
      height: 2.5rem;
      left: 1rem;
      bottom: 1rem;
    }
  }
  &::after {
    content: "";
    position: absolute;
    width: 12.5rem;
    height: 13.7rem;
    background: url(/images/common/deco-kirakira-w.svg) no-repeat center center /
      cover;
    bottom: -2rem;
    right: 2rem;

    @include mq("sm") {
      width: 5.3rem;
      height: 5.8rem;
      right: 1rem;
      bottom: 0;
    }
  }
}

.general_box {
  margin: 6rem auto 0;
  padding: 5.5rem 6rem 6rem 6rem;
  width: 100rem;
  background-color: #fff;
  border-radius: 20px;
  position: relative;

  @include mq('sm') {
    width: 100%;
    padding: 2.4rem 2rem 2rem;
    margin: 4rem auto 0;
  }

  &:nth-of-type(n + 2) {
    margin: 4.1rem auto 0;

    @include mq('sm') {
      margin: 3.1rem auto 0;
    }
  }
  &:nth-of-type(n + 3) { 
    @include mq('sm') {
      margin: 2rem auto 0;
    }
  }


  &::before {
    content: "";
    position: absolute;
    width: 10rem;
    height: 8rem;
    background: url(/images/medical/tag-01.svg) no-repeat center center / cover;
    top: 0;
    right: 6rem;

    @include mq('sm') {
      width: 8rem;
      height: 6rem;
      background: url(/images/medical/tag-01-sp.svg) no-repeat center center / cover;
      top: 1%;
      right: 6%;
    }
  }
  &.special_box::before {
    background: url(/images/medical/tag-02.svg) no-repeat center center / cover;

    @include mq('sm') {
      background: url(/images/medical/tag-02-sp.svg) no-repeat center center / cover;
    }
  }
}

.general_title_wrap {
  display: flex;
  align-items: center;

  @include mq('sm') {
    display: block;
  }
}

.general_box_title {
  font-size: 2.8rem;
  font-weight: bold;
  letter-spacing: 2.24px;
  margin-right: 4rem;
  display: inline-block;

  @include mq('sm') {
    font-size: 2rem;
    margin-right: 0;
    margin-bottom: 2rem;
  }
}

.general_sub_title {
  font-size: 1.4rem;
  font-weight: bold;
  letter-spacing: 1.12px;
  color: $main_color;
  display: block;
}

.general_contents {
  width: 88rem;
  border-top: 1px solid #c2c2c2;
  padding-top: 4rem;
  margin-top: 3.6rem;
  display: flex;
  justify-content: space-between;

  @include mq('sm') {
    display: block;
    width: 100%;
    margin-top: 1.7rem;
    padding-top: 2rem;
  }
}

.general_text {
  width: 47.772%;
  font-size: 1.4rem;
  font-weight: 500;
  margin-right: 4.6%;
  line-height: 1.7142;

  @include mq('sm') {
    width: 100%;
  }
}

.general_img {
  width: 47.7%;

  @include mq('sm') {
    width: 100%;
    margin-top: 1.6rem;
  }
}

.lower_head_container.lower_head_medical {
  @include mq('sm') {
    margin: 1rem auto 0;
  }
}

.general_container {
  @include mq('sm') {
    padding-top: 3.5rem;
  }
}
