@use "global" as *;

.recommend_container {
  margin-top: 12rem;

  @include mq("sm") {
    margin-top: 0;
  }
}

.recommend_contents {
  display: flex;
  justify-content: center;
  margin-top: 6rem;

  @include mq("md") {
    display: block;
    width: 45rem;
    margin: 6rem auto 0;
  }
  @include mq("sm") {
    width: 100%;
    margin: 3.7rem auto 0;
  }
}

.recommend_box {
  width: 28.4rem;

  @include mq("md") {
    width: 100%;
    margin: 0 auto;
  }
  @include mq("sm") {
    width: 28rem;
  }

  &:not(:last-child) {
    margin-right: 6.2rem;

    @include mq("md") {
      margin: 0 auto 6rem;
    }
  }
}

.recommend_number {
  width: 17.7rem;
  margin: 0 auto 1.1rem;

  @include mq("md") {
    width: 60%;
  }
  @include mq("sm") {
    width: 17.7rem;
  }
}

.recommend_text {
  margin-top: 2.1rem;
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 1.714;

  @include mq("sm") {
    line-height: 1.65;
  }

  span {
    display: block;
    color: $accent_color-01;
  }
}
