@use "global" as *;

.breadcrumbs {
    width: 89.5%;
    margin: 1.4rem auto 0;
    font-size: 1.2rem;

    @include mq('sm') {
        margin-top: 1rem;
        font-weight: 500;
    }

    a:nth-of-type(1) {
        color: #C2C2C2;
    }

    div:nth-of-type(1) {
        display: inline-block;
    }

    span {
        padding: 0 0.8rem;

        @include mq('sm') {
            padding: 0 0.5rem;
        }
    }
}
