@use "global" as *;

.contact.contact_web {
    margin-bottom: 15.6rem;

    @include mq('sm') {
      margin-bottom: 10rem;
    }
}

.contact_form.contact_form_web {
  margin-top: 6.5rem;

  @include mq('sm') {
    margin-top: 7rem;
  }
}

.web_contact_content_wrap {
  &:not(:first-child) {
    margin-top: 4rem;
    margin-bottom: 15.5rem;

    @include mq('sm') {
      margin-bottom: 10rem;
    }
  }

  span {
    font-size: 1.2rem;
    font-weight: 500;
    letter-spacing: 0.1px;
    color: $main_color;
    margin-left: 4.5rem;
    display: block;
    margin-bottom: 2rem;
  }
}

.contact_head.web_contact_head {
  margin: 0;
  font-size: 1.4rem;
  font-weight: 500;
}

.web_contact_title {
  font-size: 2.8rem;
  font-weight: bold;
  letter-spacing: 2.24px;
  margin-bottom: 2.4rem;

  @include mq('sm') {
    font-size: 2rem;
    letter-spacing: 1.6px;
  }
}

.header_nav_tel.web_contact_tel {
  position: relative;
  padding-left: 3.2rem;

  &::before {
    margin-left: 3.2rem;
  }
}

.contact_dl_row {
  display: flex;
  align-items: center;
}

.contact_form_radio {
  &:first-child {
    margin-right: 3rem;

    @include mq('sm') {
      margin-right: 3.8rem;
    }
  }
}

.contact_form_radio.web_contact_form_radio {
  @include mq('sm') {
    display: inline-block;
    margin-top: 1rem;
  }
}

.contact_form_input.web_contact_form_input_radio {
  padding: 0 0 2.6rem;
}

.contact_radio_button {
  position: relative;
  font-size: 1.4rem;
  font-weight: 500;
  margin-left: 3.2rem;

  &::before {
    content: "";
    position: absolute;
    width: 2.4rem;
    height: 2.4rem;
    border: 1px solid $main_color;
    border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
    left: -3.2rem;
  }
  &::after {
    content: "";
    position: absolute;
    width: 1.2rem;
    height: 1.2rem;
    background: $main_color;
    top: 50%;
    transform: translateY(-50%);
    left: -2.6rem;
    border-radius: 50%;
    opacity: 0;
    transition: all 0.3s ease;
  }
}

.contact_radio:checked + .contact_radio_button::after {
  opacity: 1;
}

.contact_form_medical {
  height: 4rem;
}

.how_medical {
  font-size: 1.1rem;
  font-weight: 500;
  color: #888888;
}

.contact_form_medical > span {
  width: 7.4rem;
  display: inline-block;
}

.contact_checkbox {
  display: inline-block;
  margin-right: 3rem;
  margin-bottom: 1.6rem;

  &:nth-last-child(-n + 2) {
    margin-bottom: 0;
  }
  &:nth-child(4n) {
    margin-right: 0;

    @include mq('md') {
      margin-right: 1.5rem;
    }
  }

  @include mq('sm') {
    margin-right: 1.5rem;
  }
}

.contact_checkbox_item {
  position: relative;
  font-size: 1.4rem;
  font-weight: 500;
  margin-left: 3.2rem;

  &::before {
    content: "";
    position: absolute;
    width: 2.4rem;
    height: 2.4rem;
    border: 1px solid $main_color;
    top: 50%;
    transform: translateY(-50%);
    left: -3.2rem;
  }
  &::after {
    content: "";
    position: absolute;
    width: 1.4rem;
    height: 0.8rem;
    top: 0.5rem;
    left: -2.7rem;
    border-bottom: 2px solid $main_color;
    border-left: 2px solid $main_color;
    transform: rotate(-45deg);
    opacity: 0;
    transition: all 0.3s ease;
  }
}

.contact_checkbox_box:checked + .contact_checkbox_item::after {
  opacity: 1;
}

.contact_select_wrap {
  width: 100%;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    right: 1.1rem;
    top: 2.3rem;
    width: 2rem;
    height: 2rem;
    background: url(/images/contact/select-arrow.svg) no-repeat center center /
      cover;
    transform: translateY(-50%);
  }
}

.contact_select {
  width: 100%;
  padding: 1.3rem 1.6rem;
  background-color: #f6f6f6;
  border-radius: 4px;
  font-size: 1.6rem;
  font-weight: 500;
}

.flatpickr-current-month .numInputWrapper {
  font-size: 2rem;
  width: 7rem;
}

.contact_form_calendar {
  position: relative;
  &:not(:last-child) {
    margin-bottom: 1.5rem;
  }

  &::after {
    content: "";
    position: absolute;
    width: 2rem;
    height: 2rem;
    top: 50%;
    transform: translateY(-50%);
    right: 1.2rem;
    pointer-events: none;
    background: url(/images/contact/icon-calendar.svg) no-repeat center center /
      cover;
  }
}

.contact_form_input.web_contact_form_input_check {
  margin-top: 1.3rem;
  padding: 0 0 1.8rem;
}
