/* リキッドレイアウト対応 */
@use "global" as *;

html {
  font-size: 62.5%;
  
  @include mq("lg") {
    font-size: vw(strip-unit($inner), 10);
  }
  // @include mq("md") {
  //   font-size: 10px;
  // }
  @include mq('sm') {
    font-size: 10px; //600以下
  }
  @media (max-width: 375px) {
    font-size: vw(375, 10);
  }
