@use "global" as *;

/*------------------------
院長の挨拶
-------------------------*/

.director {
  margin-top: 8rem;

  @include mq('sm') {
    margin-top: 6.3rem;
  }
}

.director_contents {
  width: 100rem;
  margin: 6rem auto 0;
  display: flex;

  @include mq('sm') {
    width: 100%;
    margin: 4.2rem auto 0;
    line-height: 1.6;
    display: block;
    padding: 0 2rem;
  }
}

.director_contents_left {
  width: 46%;
  margin-right: 8%;

  @include mq('sm') {
    width: 100%;
  }
}

.director_contents_head {
  font-size: 2.8rem;
  font-weight: bold;
  letter-spacing: 2.24px;

  @include mq('sm') {
   font-size: 2rem;
   letter-spacing: 1.6px; 
  }
}

.director_contents_text {
  margin-top: 3.5rem;
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 1.7142;

  @include mq('sm') {
   margin-top: 2.6rem; 
  }
}

.director_contents_name {
  margin-top: 1.6rem;
  font-size: 1.4rem;
  font-weight: 500;
  text-align: right;
  padding-bottom: 0.9rem;

  @include mq('sm') {
   margin-top: 3.7rem;
   padding-bottom: 0;
  }
}

.director_contents_title {
  font-size: 1.6rem;
  font-weight: 500;
  border-bottom: 1px solid #393939;
  margin-top: 5.3rem;
  padding-bottom: 0.5rem;

  @include mq('sm') {
    margin-top: 4.2rem;
  }

  &.directory_contents_title_skill {
    margin-top: 3.2rem;

    @include mq('sm') {
    margin-top: 4rem;
    }
  }
}

.director_contents_right {
  width: 46%;

  @include mq('sm') {
    width: 100%;
    margin-top: 3.6rem;
  }
}

.director_contents_bottom_sp {
  display: none;
  padding: 0 2rem;

  @include mq('sm') {
    display: block;
  }
}

.director_contents_career_list {
  display: flex;
  font-size: 1.4rem;
  font-weight: 500;
  margin: 1.6rem 0 1.6rem;

  dt {
    margin-right: 1.6rem;
  }
}

.director_contents_skill {
  li {
    font-size: 1.4rem;
    font-weight: 500;
    margin-top: 1.4rem;
  }
}

.director_contents_bottom {
  @include mq('sm') {
    display: none;
  }
}

/*------------------------
スタッフスライダー
-------------------------*/

.staff_slider {
  width: 100%;
  margin-top: 16rem;

  @include mq('sm') {
   margin-top: 10rem; 
  }
}

.slider_staff_img {
  margin: 0 1rem;

  img {
    @include mq('sm') {
      width: 100%;
      height: auto;
    }
  }
}

/*------------------------
スタッフ紹介
-------------------------*/

.staff_introduction {
  margin-top: 16.7rem;
  margin-bottom: 10rem;

  @include mq('sm') {
   margin-top: 10.5rem;
   margin-bottom: 9.5rem; 
  }
}

.staff_introduction_inner {
  width: 100rem;
  margin: auto;

  @include mq('sm') {
    width: 100%;
    padding: 0 2rem;
  }
}

.staff_introduction_contents {
  margin-top: 5.7rem;

  @include mq('sm') {
   margin-top: 4.3rem; 
  }

  &.staff_introduction_contents_02 {
    margin-top: 0.2rem;

    @include mq('sm') {
    margin-top: 3.8rem;
    }
  }
}

.staff_introduction_job {
  font-size: 1.6rem;
  font-weight: bold;
  letter-spacing: 1.28px;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #707070;
}

.staff_introduction_box {
  padding: 4rem 0 6rem;
  display: flex;
  flex-wrap: wrap;

  @include mq('sm') {
    display: block;
    padding-bottom: 0;
  }
}

.staff_card {
  width: 28%;

  @include mq('sm') {
    width: 100%;
  }

  &:not(:nth-child(3n)) {
    margin-right: 8%;

    @include mq('sm') {
    margin-right: 0;
    }
  }
  &:nth-of-type(n + 4) {
    margin-top: 6rem;
    @include mq('sm') {
    margin-top: 0;
    }
  }
  &:nth-of-type(n + 2) {
    @include mq('sm') {
    margin-top: 5.2rem;
    }
  }
}

.staff_card_head {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.6rem 0;
}

.staff_card_job {
  font-size: 1.2rem;
  font-weight: 500;
  letter-spacing: 0.96px;
  margin-right: 1.2rem;
}

.staff_card_name {
  font-size: 1.8rem;
  font-weight: bold;
  letter-spacing: 1.88px;
}

.staff_card_profile {
  font-size: 1.4rem;
  font-weight: 500;
}

.staff_card_profile_list {
  display: flex;

  dt {
    background-color: #dff1fd;
    width: 14rem;
    padding: 1.65rem 2rem 1.65rem;
    border-bottom: 1px solid #fff;
  }
  dd {
    background-color: #fff;
    width: 14rem;
    padding: 1.65rem 2rem 1.65rem;
  }
}
