@use "global" as *;

.drawer_menu {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  opacity: 0;
  pointer-events: none;
  background-color: $sub_color-01;
  transition: all 0.3s ease;
  overflow: scroll;

  &.is_active {
    opacity: 1;
    pointer-events: all;
    z-index: 99;
  }
}

.drawer_menu_header {
  height: 60px;
  display: flex;
  align-items: center;
}

.drawer_logo {
  width: 270px;
  margin-left: 2rem;
}

.drawer_button.drawer_button_sp {
  margin-left: auto;
  margin-right: 14px;
  display: block;

  span {
    background-color: #fff;
  }
}

.drawer_menu_list {
  width: 255px;
  margin: 40px auto 0;
  padding-bottom: 100px;
}

.drawer_menu_link {
  height: 56px;
  border-top: 1px dotted $main_color;

  &:nth-of-type(6) {
    border-bottom: 1px dotted $main_color;
  }

  a {
    display: block;
    font-size: 14px;
    line-height: 56px;
    color: #fff;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      width: 14px;
      height: 14px;
      background: url(/images/common/icon-right.svg) no-repeat center center /
        cover;
      top: 50%;
      transform: translateY(-50%);
      right: 0;
    }
  }
  img {
    display: inline;
    vertical-align: text-bottom;
    width: 24px;
    height: 24px;
  }
  span {
    margin-left: 14px;
  }
}
