@use "global" as *;

.news {
  margin-top: 2.1rem;
}

.news_inner {
  display: flex;
  justify-content: center;
  width: 118rem;
  max-width: 100%;
  margin: 0 auto;
  padding: 0 2rem;

  @include mq('md') {
    flex-direction: column-reverse;
  }
}

.news_container {
  width: 53.8rem;
  margin-right: 6.3rem;

  @include mq("lg") {
    margin-right: 2rem;
  }
  @include mq("md") {
    margin: 4rem auto 0;
    width: 65%;
  }
  @include mq("sm") {
    width: vw(375, 335);
    margin: -0.8rem auto 0;
  }
}

.news_head_wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.news_head {
  font-size: 2rem;
  font-weight: bold;
  letter-spacing: 2px;
  line-height: 1.8;
  position: relative;
  margin-left: 1.2rem;

  @include mq("sm") {
    letter-spacing: 1.6px;
    margin-left: 1.5rem;
  }

  &::after {
    content: "NEWS";
    position: absolute;
    font-size: 1.2rem;
    letter-spacing: 1.2px;
    line-height: 1.8;
    color: $main_color;
    top: 1rem;
    margin-left: 1.6rem;

    @include mq('md') {
      top: 50%;
      transform: translateY(-50%);
    }

    @include mq("sm") {
      font-size: 1.1rem;
      letter-spacing: 1.1px;
      margin-left: 1.5rem;
    }
  }
}

.news_link {
  font-size: 1.2rem;
  font-weight: 500;
  line-height: 1.2;
  color: #888888;
  border-bottom: 2px solid #888888;

  @include mq("sm") {
    font-size: 1.1rem;
    padding-top: 0.5rem;
  }
}

.news_box {
  display: flex;
  align-items: center;
  padding-right: 3rem;
  border-top: 2px solid #888888;
  border-bottom: 2px solid #888888;
  position: relative;

  &:not(:first-of-type) {
    border-top: none;
  }

  &::after {
    content: "";
    position: absolute;
    background: url(/images/common/icon-arrow-black.svg) no-repeat center center /
      cover;
    width: 1.4rem;
    height: 1.4rem;
    top: 50%;
    transform: translateY(-50%);
    right: 0.8rem;
    transition: right 0.3s ease;
  }

  &:hover {
    color: $main_color;
  }
  &:hover::after {
    background: url(/images/common/icon-arrow-blue-btn.svg) no-repeat center
      center / cover;
    right: 0;
  }
}

.news_date {
  font-size: 1.2rem;
  font-weight: 500;
  line-height: 3;
  margin-left: 1.2rem;

  @include mq("sm") {
    margin-left: 0.8rem;
  }
}

.news_title {
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 2.5714;
  margin-left: 2.4rem;

  @include mq("sm") {
    line-height: 1.4;
    padding: 1.2rem 0;
  }
}

.time_table {
  width: 47.7rem;
  box-shadow: 1px 1px 10px #888888;
  border-radius: 20px;
  position: relative;
  top: -5.2rem;

  @include mq("md") {
    width: 65%;
    top: 0;
    margin: auto;
  }

  @include mq("sm") {
    width: vw(375, 335);
    top: -4.3rem;
    margin: 0 auto;
  }
}
