@use "global" as *;

.footer_bg {
  @include mq("sm") {
    background: url(/images/common/wave.svg) no-repeat center center / cover;
    width: 100%;
    height: 3.2rem;

    img {
      display: none;
    }
  }
}

.footer_inner {
  background-color: $sub_color-02;
  padding-top: 5rem;

  @include mq("sm") {
    padding-top: 1.5rem;
  }
}

.footer_container {
  width: 100rem;
  background-color: #fff;
  padding: 4rem 4.9rem;
  margin: 1rem auto 0;
  border-radius: 20px;

  @include mq("md") {
    width: 80%;
  }
  @include mq("sm") {
    width: 94%;
    border-radius: 0;
    padding: 3.2rem 1rem 2rem;
    margin: 0 auto 0;
  }
}

.footer_contents {
  display: flex;
  justify-content: center;

  @include mq("md") {
    display: block;
  }
}

.footer_contents_left {
  width: 54%;
  margin-right: 4rem;

  @include mq("md") {
    width: 100%;
  }
  @include mq("sm") {
    margin-right: 0;
  }
}

.footer_logo {
  width: 40.2rem;
  margin: auto;

  @include mq("md") {
    width: 70%;
  }
  @include mq("sm") {
    width: 92%;
  }
}

.footer_address {
  font-size: 1.4rem;
  font-weight: 500;
  text-align: center;
  margin-top: 0.8rem;
}

.footer_tel {
  width: 100%;
  font-size: 3.2rem;
  font-weight: bold;
  letter-spacing: -0.64px;
  color: $main_color;
  text-align: center;
  margin: 1.5rem auto 0;
  padding-left: 2.4rem;
  position: relative;

  @include mq("md") {
    padding-left: 0;
  }
  @include mq("sm") {
    margin: 2rem auto 0;
    padding-left: 2.9rem;
    width: 25.8rem;
  }

  &::before {
    content: "";
    position: absolute;
    width: 2.8rem;
    height: 2.8rem;
    background: url(/images/common/icon_tel.svg) no-repeat center center / cover;
    top: 50%;
    left: 23%;
    transform: translateY(-50%);

    @include mq("md") {
      left: 32%;
    }
    @include mq("sm") {
      left: -1%;
    }
  }
}

.footer_hours {
  display: block;
  font-size: 1.2rem;
  font-weight: 500;
  color: $main_color;
  text-align: center;
  margin: -0.5rem 0 0 0;
}

.footer_button_wrap {
  width: 46rem;
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  margin-left: 1rem;

  @include mq("md") {
    width: 100%;
    margin-left: 0;
  }
}

.footer_web_button {
  width: 22rem;
  height: 5.6rem;
  text-align: center;
  border-radius: 30px;
  background-color: $main_color;
  position: relative;
  transition: background-color 0.3s ease;
  margin-right: 2rem;

  @include mq("md") {
    margin-right: 4rem;
  }
  @include mq("sm") {
    width: 15rem;
    height: 3.8rem;
    margin-right: 1.5rem;
  }

  a {
    display: inline-block;
    font-size: 1.6rem;
    font-weight: 500;
    color: #fff;
    line-height: 5.6rem;
    width: 100%;
    cursor: pointer;
    padding-left: 3rem;

    @include mq("sm") {
      line-height: 3.8rem;
      font-size: 1.4rem;
      font-weight: 400;
      padding-left: 3rem;
    }
  }

  &::before {
    content: "";
    position: absolute;
    background: url(/images/common/icon-pc.svg) no-repeat center center / cover;
    width: 2.6rem;
    height: 1.5rem;
    top: 50%;
    transform: translateY(-50%);
    left: 6rem;
    transition: right 0.3s ease;

    @include mq("sm") {
      left: 3rem;
    }
  }

  &:hover {
    background-color: #0060a0;
  }
}

.footer_contact_button {
  width: 22rem;
  height: 5.6rem;
  text-align: center;
  border-radius: 30px;
  background-color: #fff;
  border: 1px solid $main_color;
  position: relative;
  transition: background-color 0.3s ease;

  @include mq("sm") {
    width: 15rem;
    height: 3.8rem;
  }

  a {
    display: inline-block;
    font-size: 1.6rem;
    font-weight: 500;
    color: $main_color;
    line-height: 5.6rem;
    width: 100%;
    cursor: pointer;
    padding-left: 3rem;

    @include mq("sm") {
      font-size: 1.4rem;
      font-weight: 400;
      line-height: 3.8rem;
    }
  }

  &::before {
    content: "";
    position: absolute;
    background: url(/images/common/icon-mail.svg) no-repeat center center /
      cover;
    width: 2.5rem;
    height: 2.4rem;
    top: 50%;
    transform: translateY(-50%);
    left: 4.2rem;
    transition: right 0.3s ease;

    @include mq("sm") {
      left: 2.1rem;
      width: 1.7em;
      height: 1.7rem;
    }
  }

  &:hover {
    background-color: $main_color;

    a {
      color: #fff;
    }
  }
  &:hover::before {
    background: url(/images/common/icon-07.svg) no-repeat center center / cover;
    width: 2.9rem;
    height: 2.6rem;
    left: 4rem;
  }
}

.footer_timetable {
  width: 47.7rem;
  margin-top: 2rem;
  border-radius: 12px;
  box-shadow: 0px 3px 6px #00000029;

  @include mq("md") {
    width: 80%;
    margin: 4rem auto 0;
  }
  @include mq("sm") {
    width: 100%;
    margin-top: 2rem;
  }
}

.footer_contents_right {
  width: 42.7%;

  @include mq("md") {
    width: 80%;
    margin: 4rem auto 0;
  }
  @include mq("sm") {
    width: 94%;
    margin-top: 2rem;
  }
}

.footer_map {
  margin-top: 1.5rem;
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;

  @include mq("md") {
    padding-top: 70%;
  }
  @include mq("sm") {
    padding-top: 100%;
  }

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.footer_bottom {
  width: 100rem;
  margin: 4rem auto 0;
  display: flex;
  justify-content: center;
  font-size: 1.6rem;
  line-height: 1.4;

  @include mq("md") {
    // display: block;
    width: 85%;
    font-size: 1.8rem;
    margin: 8rem auto 0;
  }
  @include mq("sm") {
    display: block;
    margin: 6.2rem 0 0;
    padding-left: 2rem;
    font-size: 1.6rem;
  }

  p {
    font-weight: bold;
  }

  a {
    font-weight: 500;
  }

  a:hover {
    opacity: 0.7;
  }

  li {
    position: relative;
    padding-left: 1.2rem;
    margin-top: 2rem;
  }

  li::before {
    content: "";
    position: absolute;
    width: 0.6rem;
    height: 1.2rem;
    background: url(/images/common/icon-arrow-blue.png) no-repeat center center /
      cover;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
  }

  ul {
    @include mq("sm") {
      margin-bottom: 3.8rem;
    }
  }
}

.footer_top {
  margin-right: 4rem;
  a {
    font-weight: bold;
  }
  @include mq("sm") {
    margin-bottom: 4rem;
  }
}

.footer_about {
  margin-right: 4rem;
}

.footer_staff {
  margin-right: 4rem;
}

.footer_medical_wrap {
  display: flex;

  ul {
    margin-right: 2rem;
  }
}

.footer_medical {
  margin-right: 2rem;
}

.footer_copy {
  margin-top: 5.7rem;
  height: 5.5rem;
  background-color: $main_color;
  font-size: 1.4rem;
  color: #fff;
  text-align: center;
  line-height: 5.5rem;

  @include mq('lg') {
   height: 11.5rem;
   font-weight: 500;
   margin-top: 6rem; 
  }
  @include mq('md') {
   height: 15.5rem;
   font-weight: 500;
   margin-top: 6rem; 
  }
  @include mq('sm') {
   height: 11.5rem;
   font-weight: 500;
   margin-top: 6rem; 
  }
}

.to_top {
  width: 5.2rem;
  height: 5.2rem;
  position: fixed;
  z-index: 99;
  bottom: 4rem;
  right: 2rem;
  cursor: pointer;

  @include mq('sm') {
   bottom: 10.2rem;
   right: 1.3rem; 
  }
}
