@use "global" as *;

.about {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    background: url(/images/common/concept-bg.svg) no-repeat center right -42rem /
      contain;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    z-index: -1;

    @include mq("sm") {
      background: url(/images/common/concept-bg_sp.svg) no-repeat;
      width: 36.5rem;
      top: 6rem;
      right: 0;
    }
  }
}

.about_bg {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    background: url(/images/common/concept-bg.svg) no-repeat center right -42.5rem /
      contain;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    z-index: -1;

    @include mq("sm") {
      background: url(/images/common/concept-bg_sp.svg) no-repeat;
      width: 36.5rem;
      top: 6rem;
      right: 0;
    }
  }
}

.about_inner {
  max-width: 192rem;
  margin: 0 auto;
}

.about_container {
  display: flex;
  padding-top: 18rem;
  padding-bottom: 10.7rem;

  @include mq("md") {
    flex-direction: column-reverse;
    padding-top: 17rem;
  }
}

.about_img {
  width: 50%;
  height: 100%;
  margin-right: 9.5rem;

  @include mq("md") {
    width: 80%;
    height: 35rem;
    margin: 4rem auto 0;
  }

  @include mq("sm") {
    width: 89%;
    height: 22.9rem;
    margin: 3rem 0 0 0;
  }

  img {
    height: 43.8rem;
    object-fit: cover;
    object-position: bottom;
    border-radius: 0 20px 20px 0;

    @include mq("md") {
      width: 100%;
      height: 100%;
      border-radius: 20px;
      object-fit: cover;
    }
    @include mq("sm") {
      height: 22.9rem;
      border-radius: 0 20px 20px 0;
    }
  }
}

.about_content {
  width: 31.8%;
  padding: 4rem 0 3.5rem 0;

  @include mq("md") {
    width: 80%;
    margin: 0 auto;
  }

  @include mq("sm") {
    width: 100%;
    padding: 0 2rem;
  }

  span {
    display: block;
    font-size: 1.4rem;
    font-weight: bold;
    color: $main_color;
    letter-spacing: 1.4px;
    margin-bottom: 2.8rem;

    @include mq("sm") {
      font-size: 1.1rem;
      letter-spacing: 1.1px;
      margin-bottom: 1.4rem;
    }
  }
}

.about_heading {
  font-size: 2.8rem;
  letter-spacing: 2.24px;
  font-weight: bold;

  @include mq("sm") {
    font-size: 2rem;
    letter-spacing: 1.6px;
    line-height: 1.6;
  }
}

.about_text {
  font-size: 1.4rem;
  line-height: 1.748;
  font-weight: 500;
  padding: 3.5rem 0 3.3rem;

  @include mq("sm") {
    padding: 2.5rem 0;
  }
}

.about_button {
  width: 17.6rem;
  height: 4.3rem;
  text-align: center;
  border-radius: 24px;
  border: 1px solid $main_color;
  background-color: #fff;
  position: relative;
  transition: background-color 0.3s ease;

  @include mq("md") {
    width: 20rem;
    height: 5rem;
    margin: 0 auto;
  }
  @include mq("sm") {
    width: 16.4rem;
    height: 3.9rem;
    margin: 1rem auto;
  }

  a {
    display: inline-block;
    font-size: 1.4rem;
    color: $main_color;
    padding: 0.9rem 0;
    width: 100%;
    cursor: pointer;

    @include mq("md") {
      font-size: 2rem;
      padding: 1rem 0;
    }
    @include mq("sm") {
      font-size: 1.4rem;
      padding: 0.8rem 0;
    }
  }

  &::after {
    content: "";
    position: absolute;
    background: url(/images/common/icon-arrow-blue-btn.svg) no-repeat center
      center / cover;
    width: 1.4rem;
    height: 1.4rem;
    top: 50%;
    transform: translateY(-50%);
    right: 1.2rem;
    transition: right 0.3s ease;
  }

  &:hover {
    background-color: $sub_color-01;

    a {
      color: #fff;
    }
  }
  &:hover::after {
    background: url(/images/common/arrow-right-white.svg) no-repeat center
      center / cover;
    right: 0.5rem;
  }
}

/*------------------------
下層ページ
-------------------------*/

.policy {
  padding-top: 8rem;

  @include mq("sm") {
    padding-top: 6.3rem;
  }
}

.about_bg.lower_about_bg {
  margin-top: 5rem;
  position: relative;

  @include mq("sm") {
    margin-top: 4.5rem;
  }

  &::after {
    content: "";
    position: absolute;
    background: url(/images/common/concept-bg.svg) no-repeat center right -42.5rem /
      contain;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    z-index: -1;

    @include mq("md") {
      top: -15rem;
    }

    @include mq("sm") {
      background: url(/images/common/concept-bg_sp.svg) no-repeat;
      width: 36.5rem;
      top: 0;
      right: 0;
    }
  }
}

.about_container.lower_about_container {
  padding-top: 16.5rem;

  @include mq("md") {
    flex-direction: column-reverse;
    padding-top: 5rem;
  }
  @include mq("sm") {
    padding-top: 0rem;
    padding-bottom: 7rem;
  }
}

.about_container.lower_about_container.feature_container {
  flex-direction: row-reverse;
  padding-bottom: 11.5rem;

  @include mq("md") {
    flex-direction: column-reverse;
    padding-bottom: 10rem;
  }
}

.about_img.lower_about_img {
  margin-top: 2rem;

  @include mq("sm") {
    margin-top: 1rem;
  }

  img {
    border-radius: 0 20px 20px 0;

    @include mq("md") {
      border-radius: 20px;
    }
    @include mq("sm") {
      border-radius: 0 20px 20px 0;
    }
  }
}

.about_img.feature_img.lower_about_img {
  margin: 0 0 0 9.5rem;

  @include mq("sm") {
    margin: 1rem 0 0 auto;
  }

  img {
    border-radius: 20px 0 0 20px;
    @include mq("md") {
      border-radius: 20px;
    }
    @include mq("sm") {
      border-radius: 20px 0 0 20px;
    }
  }
}

.about_content.lower_about_content {
  margin-top: 1.5rem;

  @include mq("sm") {
    margin-top: 11rem;
  }
}

.about_content.feature_content {
  padding-top: 3rem;
}

.gallery_inner {
  margin-top: 2rem;
  margin-bottom: 16rem;

  @include mq("md") {
    margin-top: 15rem;
  }
  @include mq("sm") {
    margin-top: 0rem;
    margin-bottom: 9.5rem;
  }
}

.gallery_container {
  width: 99.9rem;
  margin: 5.5rem auto 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2.4rem;

  @include mq("md") {
    width: 100%;
    gap: 5rem;
    margin: 4.7rem auto 0;
  }
  @include mq("sm") {
    gap: 1.15rem;
  }
}

.gallery_card {
  width: calc(33.3333% - 2.4rem *2 / 3);

  @include mq("md") {
    width: 43%;
  }
}
