@use "global" as *;

.blog {
  padding-top: 16rem;
  padding-bottom: 16rem;

  @include mq("sm") {
    padding-top: 10.5rem;
    padding-bottom: 10rem;
  }
}

.blog_box {
  width: 100rem;
  margin: 8rem auto 0;
  display: flex;
  flex-wrap: wrap;

  @include mq("sm") {
    display: block;
    width: 100%;
    margin: 5.1rem auto 0;
  }
}

.blog_card {
  width: 30%;
  border-radius: 20px;
  box-shadow: 0px 3px 6px #00000029;
  position: relative;

  @include mq("md") {
    width: 45%;
    margin: 0 auto 2rem;
  }
  @include mq("sm") {
    width: 89%;
    border-radius: 0;
    box-shadow: none;
  }

  &:nth-of-type(-n + 2)::before {
    content: "NEW";
    position: absolute;
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    background-color: $accent_color-01;
    color: #fff;
    font-size: 1.6rem;
    font-weight: bold;
    text-align: center;
    line-height: 6rem;
    top: -2rem;
    left: -1rem;
    z-index: 1;

    @include mq("sm") {
      width: 4rem;
      height: 4rem;
      font-size: 1rem;
      line-height: 4rem;
      top: -0.8rem;
    }
  }

  &:not(:nth-child(3n + 3)) {
    margin-right: 5%;

    @include mq("md") {
      margin: 0 auto 2rem;
    }
  }

  &:nth-child(n + 4) {
    margin-top: 5rem;

    @include mq("md") {
      margin: 0 auto 2rem;
    }
  }

  &:hover img {
    transform: scale(1.1, 1.1);
  }

  &:hover .blog_title {
    color: $main_color;
  }
}

.blog_img {
  border-radius: 20px 20px 0 0;
  overflow: hidden;

  @include mq("sm") {
    border-radius: 0;
    width: 40%;
    height: 10.1rem;
  }

  img {
    transition: all 0.5s ease;

    @include mq("sm") {
      height: 10.1rem;
      object-fit: cover;
    }
  }
}

.blog_head {
  @include mq("sm") {
    display: flex;
    width: 100%;
  }
}

.blog_body {
  position: relative;
  padding: 2.5rem 2.4rem 1.6rem;

  @include mq("sm") {
    width: 60%;
    padding: 0.5rem 0.8rem 0;
  }
}

.blog_category {
  width: fit-content;
  padding: 0 10px;
  height: 2.8rem;
  background-color: $main_color;
  border-radius: 14px;
  line-height: 2.8rem;
  text-align: center;
  font-size: 1.1rem;
  color: #fff;
  position: absolute;
  top: -1.5rem;

  @include mq("sm") {
    width: 5.6rem;
    height: 1.8rem;
    border-radius: 10px;
    font-size: 1rem;
    line-height: 1.8rem;
    position: static;
    margin-bottom: 1rem;
  }
}

.blog_title {
  font-size: 1.6rem;
  line-height: 1.5;
  font-weight: 500;

  @include mq('md') {
    font-size: 16px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }

  @include mq("sm") {
    font-size: 1.4rem;
    font-weight: 500;
    line-height: 1.4285;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
}

.blog_date {
  font-size: 1.1rem;
  font-weight: 500;
  color: #888888;
  margin-top: 0.2rem;

  @include mq("sm") {
    margin-top: 0.5rem;
  }
}

.blog_button {
  width: 27.4rem;
  height: 4.3rem;
  text-align: center;
  border-radius: 24px;
  border: 1px solid $main_color;
  background-color: #fff;
  position: relative;
  transition: background-color 0.3s ease;
  margin: 6.4rem auto 0;

  @include mq("md") {
    width: 35rem;
    height: 6rem;
    margin: 6rem auto 0;
  }
  @include mq("sm") {
    width: 26.2rem;
    height: 3.9rem;
    margin: 3.6rem auto 0;
  }

  a {
    display: inline-block;
    font-size: 1.4rem;
    color: $main_color;
    padding: 0.9rem 0;
    width: 100%;
    cursor: pointer;

    @include mq("md") {
      font-size: 2rem;
      padding: 1.5rem 0;
    }
    @include mq("sm") {
      font-size: 1.4rem;
      padding: 0.8rem 0;
    }
  }

  &::after {
    content: "";
    position: absolute;
    background: url(/images/common/icon-arrow-blue-btn.svg) no-repeat center
      center / cover;
    width: 1.4rem;
    height: 1.4rem;
    top: 50%;
    transform: translateY(-50%);
    right: 1.2rem;
    transition: right 0.3s ease;
  }

  &:hover {
    background-color: $sub_color-01;

    a {
      color: #fff;
    }
  }
  &:hover::after {
    background: url(/images/common/arrow-right-white.svg) no-repeat center
      center / cover;
    right: 0.5rem;
  }
}

/*------------------------
下層ページ
-------------------------*/

.blog.lower_blog {
  margin-top: 8rem;
  padding: 0;

  @include mq('sm') {
    margin-top: 6rem;
  }
}

.lower_blog_inner {
  width: 100.9rem;
  margin: auto;
  
  @include mq('md') {
    width: 100%;
  }
}

.lower_blog_container {
  display: flex;
  justify-content: center;

  @include mq('md') {
    flex-direction: column;
    align-items: center;
  }
}

.blog_contents {
  width: 67.294%;
  margin-right: 2.5%;

  @include mq('md') {
    width: 90%;
    margin-right: 0;
  }
}

.blog_box.lower_blog_box {
  display: block;
  width: 100%;
  margin: 0;
}

.blog_card.lower_blog_card {
  width: 100%;
  border-radius: 0;
  box-shadow: none;
  margin-bottom: 1rem;

  @include mq('md') {
    margin-bottom: 2rem;
  }

  &:nth-of-type(-n + 2)::before {
    content: "NEW";
    position: absolute;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: $accent_color-01;
    color: #fff;
    font-size: 1rem;
    font-weight: bold;
    text-align: center;
    line-height: 4rem;
    top: 0rem;
    left: -0.5rem;
    z-index: 1;

    @include mq('md') {
      width: 40px;
      height: 40px;
      line-height: 40px;
    }

    @include mq("sm") {
      width: 4rem;
      height: 4rem;
      font-size: 1rem;
      line-height: 4rem;
      top: -0.8rem;
    }
  }

  &:not(:nth-child(3n + 3)) {
    margin-right: 0;

    @include mq("md") {
      margin-right: 0;
    }
  }

  &:nth-child(n + 4) {
    margin-top: 0;

    @include mq("md") {
      margin-top: 0;
    }
  }
}

.blog_head.lower_blog_head {
  display: flex;
}

.blog_img.lower_blog_img {
  width: 37.407%;
  padding: 0.5rem 0.5rem;
  border-radius: 0;

  @include mq('sm') {
    // width: 41.701%;
    width: 143px;
    padding: 0;
  }
}

.blog_body.lower_blog_body {
  width: 59.646%;
  margin-left: 1.5rem;
  padding: 2rem 0 0;

  @include mq('md') {
    padding-top: 4rem;
  }
  @include mq('sm') {
    padding-top: 0.8rem;
    margin-left: 1.2rem;
    width: 60.298%;
  }
}

.blog_title.lower_blog_title {
  margin-top: 1.8rem;

  @include mq('sm') {
    margin-top: 0.6rem;
  }
}

.blog_category.lower_blog_category {
  width: fit-content;
  padding: 0 1.2rem;
  height: 2.8rem;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 500;
  line-height: 2.8rem;
  position: static;
  margin-bottom: 1rem;

  @include mq("md") {
    height: 20px;
    line-height: 20px;
    padding: 0 5px;
  }
  @include mq('sm') {
    margin-bottom: 0;
  }
}

.blog_date.lower_blog_date {
  margin-top: 1.6rem;
  font-size: 1.2rem;

  @include mq('md') {
    font-size: 12px;
  }
  @include mq('sm') {
    font-size: 11px;
    margin-top: 0.5rem;
  }
}

/*------------------------
新着記事
-------------------------*/

.blog_img.lower_blog_img.lower_blog_img_newarticle {
  width: 40%;
  padding: 0;

  img {
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  @include mq('md') {
    padding: 1rem;
    height: 30rem;
  }
  @include mq('sm') {
    padding: 0;
    width: 133px;
    height: 101px;
  }
}

.blog_body.lower_blog_body.lower_blog_body_newarticle {
  width: 56.6%;
  padding: 0;
  margin-left: 1rem;

  @include mq('md') {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 20px;
  }
  @include mq('sm') {
    margin-left: 10px;
    margin-top: 10px;
    justify-content: stretch;
  }
}

.blog_card.lower_blog_card.lower_blog_card_newarticle {
  &::before {
    display: none;
  }

  @include mq('md') {
    height: 30rem;
  }
  @include mq('sm') {
    height: 10.1rem;
  }
}

.blog_category.lower_blog_category.lower_blog_category_newarticle {
  width: fit-content;
  height: 2rem;
  padding: 0 0.8rem;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 500;
  line-height: 2rem;
  position: static;
  margin-bottom: 0.8rem;

  @include mq('md') {
    font-size: 12px;
    margin-bottom: 10px;
    height: 3rem;
    line-height: 3rem;
    padding: 0 1rem;
  }
  @include mq('sm') {
    font-size: 10px;
    height: 1.8rem;
    line-height: 1.8rem;
    padding: 0 8px;
    margin-bottom: 5px;
  }
}

.blog_title.lower_blog_title_newarticle {
  font-size: 1.4rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  line-height: 1.42857;

  @include mq('md') {
    font-size: 16px;
  }
  @include mq('sm') {
    font-size: 14px;
  }
}

.blog_date.lower_blog_date.lower_blog_date_newarticle {
  font-size: 1.1rem;
  margin-top: 0.6rem;

  @include mq('md') {
    font-size: 12px;
  }
  @include mq('sm') {
    font-size: 11px;
    margin-top: 5px;
  }
}

/*------------------------
サイドバー
-------------------------*/

.sidebar {
  width: 29.732%;

  @include mq('md') {
    width: 90%;
