@use "global" as *;

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 10rem;
  margin-bottom: 16rem;

  @include mq('sm') {
    margin-top: 6rem;
    margin-bottom: 10rem;
    flex-wrap: nowrap;
  }
}

.pagination_item {
  width: 3.4rem;
  height: 3.6rem;
  font-size: 1.4rem;
  font-weight: 500;
  background-color: #fff;
  color: $main_color;
  border: 1px solid $main_color;
  border-radius: 4px;
  position: relative;

  @include mq('md') {
    width: 30px;
    height: 30px;
    font-size: 12px;
  }

  &:not(:last-child) {
    margin-right: 1rem;

    @include mq('sm') {
      margin-right: 0.7rem;
    }
  }

  &:first-child {
    background-color: $main_color;
    color: #fff;

    &::before {
      content: "";
      position: absolute;
      width: 1.6rem;
      height: 1.6rem;
      top: 50%;
      left: 1.7rem;
      transform: translateY(-50%);
      background: url(/images/blog/arrow-circle-left.svg) no-repeat center
        center / cover;
      pointer-events: none;

      @include mq('md') {
        width: 14px;
        height: 14px;
      }
      @include mq('sm') {
        left: 1.3rem;
      }
    }
  }

  &:last-child {
    background-color: $main_color;
    color: #fff;

    &::after {
      content: "";
      position: absolute;
      width: 1.6rem;
      height: 1.6rem;
      top: 50%;
      right: 1.7rem;
      transform: translateY(-50%);
      background: url(/images/blog/arrow-circle-right.svg) no-repeat center
        center / cover;
      pointer-events: none;

      @include mq('md') {
        width: 14px;
        height: 14px;
      }
      @include mq('sm') {
        right: 1.3rem;
      }
    }
  }

  &:nth-of-type(5) {
    @include mq('md') {
    display: none;
    }
  }
  &:nth-of-type(6) {
    @include mq('md') {
    display: none;
    }
  }
  &:nth-of-type(7) {
    @include mq('md') {
    display: none;
    }
  }

  a {
    display: block;
    text-align: center;
    line-height: 3.6rem;

    @include mq('md') {
      line-height: 28px;
    }
  }

  span {
    display: block;
    width: 100%;
    height: 100%;
    text-align: center;
    line-height: 3.6rem;
    background-color: $main_color;
    color: #fff;

    @include mq('md') {
      line-height: 28px;
    }
  }
}

.pagination_item.pagination_item_prev {
  width: 8.2rem;

  @include mq('md') {
    width: 71px;
  }

  a {
    line-height: 1.3;
    text-align: start;
    padding: 0.8rem 0 0.9rem 3.6rem;

    @include mq('md') {
      line-height: 30px;
      padding: 0 0 0 15px;
      text-align: center;
    }
  }
}

.pagination_item.pagination_item_next {
  width: 8.2rem;

  @include mq('md') {
    width: 71px;
  }

  a {
    line-height: 1.3;
    text-align: start;
    padding: 0.8rem 0 0.9rem 1.5rem;

    @include mq('md') {
      line-height: 30px;
      padding: 0 15px 0 0;
      text-align: center;
    }
  }
}

.pagination_item.pagination_item_prev.single_pagination_prev {
  width: 12.4rem;
  margin: 0;
  font-size: 1.4rem;

  a {
    padding-left: 4.6rem;

    @include mq('md') {
      padding-left: 2rem;
    }
    @include mq('sm') {
      line-height: 3.8rem;
      
    }
  }

  @include mq('md') {
    width: 100px;
    font-size: 12px;
  }

  @include mq('sm') {
    width: 12.4rem;
    height: 4rem;
    font-size: 1.4rem;
  }
}

.pagination_item.pagination_item_next.single_pagination_next {
  width: 12.4rem;
  margin-right: 1rem;
  font-size: 1.4rem;
  
  a {
    padding-left: 1.8rem;
    @include mq('md') {
      padding-left: 0rem;
    }
    @include mq('sm') {
      line-height: 3.8rem;
    }
  }

  @include mq('md') {
    width: 100px;
    font-size: 12px;
  }

  @include mq('sm') {
    margin-right: 0;
    width: 12.4rem;
    height: 4rem;
    font-size: 1.4rem;
  }
}

.pagination_item.single_pagination {
  width: 9rem;
  margin-left: 2rem;
  margin-right: 2rem;

  a {
    @include mq('sm') {
      line-height: 3.8rem;
    }
  }

  @include mq('md') {
    width: 100px;
    font-size: 12px;
  }

  @include mq('sm'){
    width: 11rem;
    height: 4rem;
    margin-left: 1.2rem;
    margin-right: 1.2rem;
    font-size: 1.4rem;
  }
}

.pagination.single_pagination {
  @include mq('sm') {
    margin-top: 4.3rem;
  }
}




