@use "global" as *;

.section_title {
  width: 48.1rem;
  margin: 0 auto;
  font-size: 2.8rem;
  font-weight: bold;
  letter-spacing: 2.24px;
  position: relative;
  text-align: center;

  @include mq('sm') {
    width: 33.7rem;
    font-size: 2rem;
    letter-spacing: 1.6px;
    line-height: 1;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    background: url(/images/common/title-deco.png) no-repeat center center /
      cover;
    width: 6.6rem;
    height: 1.7rem;
    top: 40%;
    transform: translateY(-50%);

    @include mq('sm') {
      width: 5.5rem;
      height: 1.2rem;
    }
  }
  &::before {
    left: -1.6rem;

    @include mq('sm') {
      left: -0.5rem;
    }
  }
  &::after {
    right: -1.6rem;

    @include mq('sm') {
      right: -0.5rem;
    }
  }
}

.section_title.sec_info {
    width: 30.9rem;

    @include mq('sm') {
      width: 21.3rem;
    }
}
.section_title.sec_blog {
    width: 40.1rem;

    @include mq('sm') {
      width: 28rem;
    }
}
.section_title.sec_policy {
  width: 38rem;

  @include mq('sm') {
    font-size: 2rem;
    width: 28rem;
  }
}
.section_title.sec_gallery {
  width: 30rem;

  @include mq('sm') {
    width: 23.2rem;
  }
}
.section_title.sec_general {
  width: 27rem;

  @include mq('sm') {
    width: 21rem;
  }
}
.section_title.sec_director {
  width: 37rem;

  @include mq('sm') {
    width: 28rem;
  }
}
.section_title.sec_staff {
  width: 34rem;

  @include mq('sm') {
   width: 25rem; 
  }
}
.section_title.sec_contact {
  width: 45.5rem;
  @include mq('sm') {
    width: 26rem;
    line-height: 1.2;
  }
}
.section_title.sec_contact_web {
  width: 33.5rem;
  @include mq('sm') {
    width: 26rem;
  }
}
