@use "global" as *;

.contact {
    margin-bottom: 9.5rem;
}

.contact_inner {
    width: 72.8rem;
    margin: auto;

    @include mq('sm') {
        width: 100%;
        padding: 0 2rem;
    }
}

.contact_container {
    margin-top: 7.8rem;

    @include mq('sm') {
        margin-top: 5.5rem;
    }
}

.contact_head {
    font-size: 1.4rem;
    font-weight: 500;
    line-height: 1.71428;
    margin-bottom: 15.5rem;

    @include mq('sm') {
        margin-bottom: 10.5rem;
    }

    span {
        color: $accent_color-01;
    }
}

.contact_form {
    margin-top: 7rem;
}

.contact_dl_row {
    display: flex;
    border-top: 1px solid #F6F6F6;

    @include mq('sm') {
        flex-direction: column;
    }

    &:last-child {
        border-bottom: 1px solid #F6F6F6;
    }
}

.contact_dl_row.contact_textarea {
    align-items: center;
}

.contact_form_label {
    width: 32.9670%;
    padding: 4rem 0 4rem;
    font-size: 1.6rem;
    font-weight: 500;

    @include mq('sm') {
        width: 100%;
        padding: 2.5rem 0 1.3rem;
    }

    span {
        position: relative;

        &::after {
            content: "必須";
            position: absolute;
            width: 3.6rem;
            min-width: 30px;
            height: 2.3rem;
            top: 50%;
            right: -5rem;
            transform: translateY(-50%);
            font-size: 1.2rem;
            font-weight: bold;
            background-color: #EE5A6D;
            border-radius: 4px;
            color: #fff;
            display: inline-block;
            line-height: 2.3rem;
            text-align: center;

            @include mq('md') {
                min-width: 26px;
                height: 2.5rem;
                line-height: 2.5rem;
                font-size: 1rem;
                font-weight: 500;
            }
            @include mq('sm') {
                width: 3.6rem;
                height: 2.3rem;
                line-height: 2.3rem;
                font-size: 1.2rem;
                font-weight: bold;
            }
        }
    }
}

.contact_form_input {
    width: 67.0329%;
    padding: 2.7rem 0;

    @include mq('sm') {
        width: 100%;
        padding: 0 0 2.2rem;
    }

    input {
        width: 100%;
        padding: 1.3rem 1.6rem;
        background-color: #F6F6F6;
        border-radius: 4px;
        font-size: 1.6rem;
        font-weight: 500;
    }

    input::placeholder {
        color: #C2C2C2;
    }

    textarea {
        width: 100%;
        height: 20rem;
        background-color: #F6F6F6;
        border-radius: 4px;
        padding: 1.2rem 1.6rem;
        font-size: 1.6rem;
        font-weight: 500;

        @include mq('sm') {
            height: 16rem;
        }
    }

    textarea::placeholder {
        color: #C2C2C2;
    }
}

.contact_form_button_wrap {
    margin-top: 7rem;

    @include mq('sm') {
        margin-top: 6.5rem;
    }
}

.contact_form_button {
    width: 36rem;
    margin: 0 auto;
    display: block;
    border: 1px solid $main_color;
    border-radius: 28px;
    font-size: 1.6rem;
    font-weight: 500;
    letter-spacing: 1.5rem;
    color: $main_color;
    padding: 1.6rem 0 1.6rem 1.5rem;
    text-align: center;
    transition: all 0.3s ease;

    @include mq('sm') {
        width: 27.3rem;
        font-size: 1.4rem;
        padding: 0.8rem 0;
    }

    &:hover {
        background-color: $main_color;
        color: #fff;
    }
}

