@use "global" as *;

.lower_head {
  padding: 0 2rem;

  @include mq("sm") {
    padding: 0;
  }
}

.lower_head_container {
  width: 93.5%;
  margin: 4rem auto 0;
  position: relative;

  @include mq("sm") {
    width: 89.3%;
    margin: 2rem auto 0;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    opacity: 0.2;
    border-radius: 12px;
  }

  &.lower_head_container_staff {
    margin: 3.1rem auto 0;

    @include mq('sm') {
      margin: 2rem auto 0;
    }
  }
}

.lower_title_box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;

  @include mq("sm") {
    width: 100%;
  }
}

.lower_title {
  font-size: 3.2rem;
  font-weight: bold;
  letter-spacing: 3.2px;
  color: #fff;
  margin-bottom: 2rem;

  @include mq("sm") {
    font-size: 2.4rem;
    letter-spacing: 4.8px;
    text-align: center;
    margin-bottom: 1.6rem;
  }
}

.lower_sub_title {
  font-size: 1.4rem;
  font-weight: bold;
  letter-spacing: 1.4px;
  color: #fff;
  text-align: center;

  @include mq("sm") {
    font-size: 1.2rem;
    letter-spacing: 1.2px;
  }
}
